//------------------------------------------------------------------------------------------
//---------------------------------------     ----------------------------------
//------------------------------------------------------------------------------------------
//
http.config.baseUrl = config.server

//
http.interceptor.request = (config) => {
    //
    config.header = {
        "token": uni.getStorageSync('userData').token
    }
	
}

//
http.interceptor.response = (res) => {
	
	console.log("",res);
	let code = res.statusCode;
	let err = null;
	
	//
	if(!code){
		uni.showModal({
			title: '',
			content: 'APP!',
			confirmColor: '#3CC51F',
			showCancel: false,
			success: function (msg) {
				if (msg.confirm) {
					
				}
			}
		});
		return res;
	}
	if(code != 200){
		// 
		switch(true){
			case code>=200 && code<300:
				break;
			case code>=300 && code<400:
				break;
			case code>=400 && code<500:
				err = "!"
				break;
			case code==504:
				err = "!"
				break;
			case code>=500 && code<600:
				err = "!"
				break;
		}
		if(err){
			uni.showModal({
				title: '',
				content: err,
				confirmColor: '#3CC51F',
				showCancel: false,
				success: function (msg) {
					if (msg.confirm) {
						
					}
				}
			});
			return res;
		}
	}
	
	
	//
	if(typeof resData.code == "undefined"){
		uni.showModal({
			title: '',
			content: '',
			confirmColor: '#3CC51F',
			showCancel: false,
			success: function (msg) {
				if (msg.confirm) {
					
				}
			}
		});
		return res;
	}
	
	
    // 
	switch(res.data.code){
		//
		case -1:
			let txt = ""
			//true
			//false
			if(store.state.hasLogin){
				txt = ","
				//
				store.dispatch("logout");
			}
			console.log();
			uni.showModal({
				title: '',
				content: txt,
				confirmText: "",
				confirmColor: '#3CC51F',
				success: function (msg) {
					if (msg.confirm) {
						uni.navigateTo({
							url: '/pages/user/login/login'
						});
					} else if (msg.cancel) {
						
					}
				}
			});
			break;
		//
		case 9999:
			uni.showModal({
				title: '',
				content: '',
				confirmColor: '#3CC51F',
				showCancel: false,
				success: function (msg) {
					if (msg.confirm) {
						
					}
				}
			});
			break;
		default:
			break;
	}
	
    return res;
}

