import http from './interface'
const CryptoJS = require("crypto-js");

//
export const login = (data) => {
    return http.request({
        url: '/home/v1/login',
		method:'POST',
        data,
    })
}

//
export const logout = () => {
    return http.request({
        url: '/home/v1/logout',
		method:'GET',
    })
}

//
export const phoneReg = (data) => {
    return http.request({
        url: '/home/v1/reg/phone',
		method:'POST',
        data,
    })
}


