import api from '@/common/vmeitime-http/'
import conf from '@/config.js'

export default{
	checkUpgrade(wgtVer){
		let that = this;
		plus.nativeUI.showWaiting("...");
		api.app.upgrade({version:wgtVer}).then((res)=>{
			plus.nativeUI.closeWaiting();
			let resData = res.data;
			if( parseInt(resData.code) ==0 ){                           
				//
				plus.nativeUI.confirm("",
					function(event){
						if(event.index ==0){
							console.log(''+ conf.server + resData.data.url)
							that.downWgt(conf.server + resData.data.url); //
						}                        
					} ,'',['','']);                
			} else{  
				plus.nativeUI.toast("");
			}
		}).catch((e)=>{
			plus.nativeUI.closeWaiting();
			 plus.nativeUI.toast('') ;
		})
	},
	// wgt
	downWgt(wgtUrl){
		let that = this;
		plus.nativeUI.showWaiting("...");
		plus.downloader.createDownload( wgtUrl, {filename:"_downloads/update/"}, function(d,status){
			if ( status == 200 ) { 
				//console.log("wgt"+d.filename);
				that.installWgt(d.filename); // wgt
			} else {
				//console.log("wgt");
				plus.nativeUI.alert("");
			}
			plus.nativeUI.closeWaiting();
		}).start();
	},
	
	//   
	installWgt(path){
		plus.nativeUI.showWaiting("...");
		plus.runtime.install(path,{},function(){
			plus.nativeUI.closeWaiting();        
			plus.nativeUI.alert("",function(){
				plus.runtime.restart();
			});
		},function(e){
			plus.nativeUI.closeWaiting();        
			plus.nativeUI.alert("["+e.code+"]"+e.message);
		});
	}	
}