// common/http.interceptor.js

// VueVue()vmmain.jsVue.use(httpInterceptor, app)
// Vue"this"
// installhttps://uviewui.com/components/vueUse.html
const install = (Vue, vm) => {
	// 
	Vue.prototype.$u.http.setConfig({
		baseUrl: 'https://h5.xiaocantong.net/',
		loadingText: '~',
		loadingTime: 800,
		method: 'GET',
		// jsonJSON.parse()
		dataType: 'json',
		showLoading: true, // loading
		originalData: false, // 
		loadingMask: true, // loading
		// 
		header: {
			'content-type': 'application/json;charset=UTF-8'
		},
	});
	// 
	Vue.prototype.$u.http.interceptor.request = (config) => {
			// token
			// vuextokenuViewvuex
			// https://uviewui.com/components/globalVariable.html
			// config.header.token = vm.token;
			
			// uViewvuex$store.state
			// config.header.token = vm.$store.state.token;
			
			// tokenglobalDatagetApp().globalData
			// config.header.token = getApp().globalData.username;
			
			// tokenStorage
			// Storage
			const token = uni.getStorageSync('token');
			config.header.token = token;
			// config.header.Token = 'xxxxxx';
			
			// urlurlthis.$u.get(url)url
			// if(config.url == '/api/shop_setting/index') config.header.noToken = true;
			// configreturn
			return config;
			// returnfalse
			// if(config.url == '/user/rest') return false; // 
		}
		
		// 
		Vue.prototype.$u.http.interceptor.response = (res) => {
			console.log('res',res)
			if(res.code == 1) {
				// rescoderesult
				// res.resultthis.$u.post(url).then(res => {})thenres
				// originalDatatrue
				return res;
			} else if(res.code == 401) {
				// 201token
				console.log('')
				vm.$u.toast('');
				setTimeout(() => {
					// uView
					vm.$u.route('/pages/user/login/login')
				}, 1500)
				return false;
			} else {
				vm.$u.toast(res.msg)
				// falsePromisereject
				// this.$u.post(url).then().catch(res=>{})catchres
				return false;
			}
		}
}

export default {
	install
}
